import React, { useState } from "react";
import Modal from "react-bootstrap/Modal";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Link } from "react-router-dom";

const ProductList = (props) => {
  const [obj, setObj] = useState({});
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      {props.subproduct ? (
        <>
          <Row xs={1} md={2} className="g-4">
            {props.data.map((object, idx) => (
              <Col key={`cntr-${object.idx}`}>
                <Card>
                  {props.showImage && (
                    <>
                      <Card.Img
                        className="card-img-top  card-popup"
                        variant="top"
                        src={object.src}
                        alt={object.name}
                        onClick={() => {
                          setObj(object);
                          handleShow();
                        }}
                      />
                    </>
                  )}
                  <Card.Body>
                    <Card.Title>
                      {object.url ? (
                        <Link to={object.url}>{object.name}</Link>
                      ) : (
                        <>{object.name}</>
                      )}
                    </Card.Title>
                    {/* <Card.Text>
                      This is a longer card with supporting text below as a
                      natural lead-in to additional content. This content is a
                      little bit longer.
                    </Card.Text> */}
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </>
      ) : (
        <>
          {props.data.map(function (object, i) {
            return (
              <div key={`cntr-${object.id}`} className="col-sm-4 my-card">
                <div className="card">
                  {props.showImage && (
                    <>
                      <img
                        src={object.src}
                        className="card-img-top  card-popup"
                        alt={object.name}
                        onClick={() => {
                          setObj(object);
                          handleShow();
                        }}
                      />
                    </>
                  )}

                  <div className="card-body">
                    <h5 className="card-title">
                      {object.url ? (
                        <Link to={object.url}>{object.name}</Link>
                      ) : (
                        <>{object.name}</>
                      )}
                    </h5>
                    {object.details.map((txt) => {
                      return <p className="card-text">{txt}</p>;
                    })}
                  </div>
                </div>
              </div>
            );
          })}
        </>
      )}

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{obj.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Row xs={1} md={2} className="g-4">
            <Col>
              <img
                src={obj.src}
                className="card-img-top  card-popup"
                alt={obj.name}
              />
            </Col>
            <Col>
              <p>PER100: {obj.per_100}</p>
              <p>Product ID: {obj.productid}</p>
              <p>Size: {obj.size}</p>
              <p>Thickness: {obj.thickness}</p>
              <p>Weight: {obj.weight}</p>
            </Col>
          </Row>
          <Row xs={1} md={2} className="g-4">
            <Col></Col>
            <Col>
              <Link to="/contact">
                <button className="btn btn-primary">Connect with us</button>
              </Link>
            </Col>
          </Row>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default ProductList;
