
import Hoc from "../../components/hoc";
import ProductList from "../../components/list";
import Header from "../../components/Header";
import data from "../../data/BRICKS.json";
import ProductListView from "../../layout/ProductList";
import CategoryBanner from "../../layout/CategoryBanner";

const Bricks = (props) => {
  const MyComponent = Hoc(ProductList, data, true, true);
  const breadcrumbs = [
    { title: "Home", url: "/" },
    { title: "Products", url: "/products" },
    { title: "TERRACOTTA CLAY Product", url: "/clay" },
    { title: "Bricks", url: "" },
  ];
  return (
    <Header pageTitle="Bricks" breadcrumbs={breadcrumbs} currentPage="Bricks">
      <CategoryBanner
        src="./assets/img/abc/title.png"
        cls="img-fluid"
        alt="Bricks"
      />
      <ProductListView
        cmp={<MyComponent />}
        description={"About the Product"}
      />
    </Header>
  );
};

export default Bricks;
